<template>
	<view>
		<!-- 1	名称 -->
		<view class="title">
			{{goodsInfo.name}}
		</view>
		<!-- 2	商品图片 -->
		<image :src="goodsInfo.pic" mode="" class="img"></image>
		<!-- 3	信息介绍 -->
		<view class="info">
			<view class="money">
				<text class="price">销量:	{{goodsInfo.price}}</text>
				<text class="number">价格:	{{goodsInfo.bynum}}</text>
			</view>
			<view class="desc">
				<view class="goodstitle">
					产品产地：{{goodsInfo.city}}
				</view>
				<view class="list">
					<view class="content">1) {{goodsInfo.description}}</view>
					<view class="content">2) 100%桑蚕丝填充，丝丝精粹不掺杂</view>
					<view class="content">3) 创新井字拉网工艺，桑蚕丝不易黏连结块</view>
					<view class="content">4) 两种面料可选，贡缎手感细腻，竹棉清爽透气</view>
				</view>
			</view>
		</view>
		<!-- 4	加购信息 -->
		<!-- 安装ui库使用商品导航 -->
		<!-- height 占高 -->
		<view class="height">
			<!-- 始终固定在屏幕底部 -->
			<view class="addGoods">
				<!-- options	    Array	-	组件参数 -->
				<!-- buttonGroup	Array	-	组件按钮组参数 -->
				<!-- fill	    Boolean	false	按钮是否平铺 -->
				<uni-goods-nav :options="options" :fill="true" :button-group="buttonGroup"
				@click="onClick" 
				@buttonClick="buttonClick()"
				/>
			</view>
		</view>
	</view>
</template>

<script>
	import { mapMutations } from "vuex"
	export default {
		data() {
			return {
				//正常:传递id标识,进入详情界面获取接口
				//非正常:点击商品时,传递了整个商品的信息,整个商品数据传递给详情页,无需再次请求 ---省事
				goodsInfo:{},//详情数据
				options: [{
					icon: 'chat',
					text: '客服'
				}, {
					icon: 'shop',
					text: '店铺',
				}, {
					icon: 'cart',
					text: '购物车',
					info: 0
				}],
				buttonGroup: [
					{
						text: '加入购物车',
						backgroundColor: 'linear-gradient(90deg, #FFCD1E, #FF8A18)',
						color: '#fff'
					},
					{
						text: '立即购买',
						backgroundColor: 'linear-gradient(90deg, #FE6035, #EF1224)',
						color: '#fff'
					}
				],
			}
		},
		onLoad(options){
			// this.goodsInfo=JSON.parse(options.goodsInfo);
			console.log('options',options);
			this.goodsDetail(options.id);
		},
		methods:{
			// 加购物车
			...mapMutations('cart' ,['addCart']),
			//获取商品详情
			async goodsDetail(id){
				let res = await uni.$http(uni.$base.goodsDetail,{loading:true},'get',{id})
				console.log('商品详情',res);
				this.goodsInfo = res.data[0];
			},
			//点击加入购物车左侧列表
			onClick(e){
				console.log('e--ee',e);
				//进入购物界面--tabar页面
				if(e.index===2){
					// uni.switchTab可以跳转tabar
					uni.switchTab({
						url:'/pages/cart/cart'
					})
				}
			},
			//点击加入购物车-立即购买按钮
			/*
				参数	是	name	string	产品名称
				参数	是	pic	string	商品的图片名字
				参数	是	num	number	商品数量-可以默认1
				参数	是	info	string	商品的简介
				参数	是	price	string	商品的价格--加入购物车价格固定的，目前不支持选择价格区间
			 */
			async cartAdd(){
				//获取数据
				let { name,pic,price,id} = this.goodsInfo;
				// 当前数据字段与后台给的字段不同，需要重新定义
				let info = this.goodsInfo.description;
				let res = await uni.$http(uni.$base.cartAdd,{loading:false,title:'加入成功'},'get',{
					name,pic,num:1,price,info
				})
				console.log('加入购物车----',res);
				/*
					id: 12
					info: "北京特产御食园茯苓饼500g传统茯苓夹饼休闲零食糕点心小吃美食"
					name: "北京特产御食园茯苓饼500g"
					num: 1
					pic: "http://iwenwiki.com:3002/images/goods/6.webp"
					price: "16.90"
				 */
				// 思路:通过id传,不能直接加到仓库里,
				//此项目使用的方法不合理但是由于给的数据有可以传,所以可以直接用,
				let obj={
					id,
					name,
					pic,
					price,
					info,
					num:1
				}
				this.addCart(obj);
			},
			//点击加入购物车或者立即购买按钮--事件
			buttonClick(e) {
				console.log('buttonClick---', e);
				this.options[2].info++;
				//加入购物车
				if(e.index===0){
					//请求加入购物车的数据--cartAdd
					this.cartAdd();
				}else{
					//立即购买----
				}
			}
		}
	}
</script>

<style lang="scss" scoped>
